
.authentication {
	background: #DADADA;
	&.login,
	&.register,
	&.signedup,
	&.email {
		> .grid {
			height: 100%;
		}
		.column {
		  	max-width: 450px;
		}
		.forgotten {
			margin-top: 20px;
		}
		.ui.stacked.segment:after {
			border-top: none;
		}
	}
	.ui.form .error.message,
	.ui.form .success.message,
	.ui.form .warning.message {
		display: block;
	}
}

#screen-loader {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: white;
	z-index: 9999;
}